@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// 產險商品 3 x 2
.cathay-carosel{
  &.#{$prefix}-layout-05{
    
    padding-top: 25px;

    @media (min-width: map-get($grid-breakpoints, 'md')){
      padding-top: 0px;
      // padding-bottom: 0;
    }

    .#{$prefix}slider{
      display: flex;
      flex-wrap: wrap;
      >div,>a{
        flex: 1 1 100%;
        padding:0px 20px;
        position: relative;
        margin-bottom:10px;
      }
    }
    .#{$prefix}link{
     &:hover{
       opacity:1;
     } 
    }
    .#{$prefix}title{
      font-size:18px;
      font-weight: bold;
      line-height: 1;
      color:#fff;
      display:inline-block;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        display:block;
        font-size:20px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        font-size:24px;
      }
    }
    

    .#{$prefix}subtitle{
      font-size:18px;
      font-weight: 400;
      line-height: 1;
      color:#333;
      font-weight: bold;
      display:inline-block;
      margin-left:5px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        display:block;
        margin-top:12px;
        margin-left:0px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        font-size:24px;
      }
    }

    .#{$prefix}content{
      display:none;
    }
    .#{$prefix}txt-wrap{
      position: relative;
      z-index: 100;
      background-color:#8bce95;
      display: block;
      position: relative;
      text-align:left;
      width: 95%;
      margin-left:5%;
      padding:23px 0px 23px 60px;
      border-radius: 10px;
      transition: background-color .15s;
      transition-timing-function: ease-in-out;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: -45px;
        text-align:center;
        padding:70px 20px 40px 20px;
        border-radius: 25px;
        width: 100%;
        margin-left:0%;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        margin-top:-65px;
        padding:90px 20px 70px 20px;
      }
    }
    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
    }

    .#{$prefix}img-wrap{
      background-color:#fff;
      border-radius:50%;
      -webkit-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      -moz-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      background-size: cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      margin: 0 auto;
      vertical-align: middle;

      img{
        visibility: hidden;
      }

      $ratio:.25;
      width: round($ratio * 260px);
      height: round($ratio * 260px);
      
      position: absolute;
      top:0;
      bottom:0;
      margin-top:auto;
      margin-bottom:auto;
      left:5%;
      z-index: 200;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        $ratio:0.35;
        width: round($ratio * 260px);
        height: round($ratio * 260px);
        position: relative;
        left:auto;
        top:auto;
        bottom:auto;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        $ratio:0.5;
        width: round($ratio * 260px);
        height: round($ratio * 260px);
        position: relative;
        left:0;
      }
    }
    
     .slick-slide {
        margin: 0 12px;
        padding:20px 0px;

        &:hover{

          .#{$prefix}txt-wrap{
              background-color:#6ea977;
          }
        }
      }
    

    .slick-track{
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
}